@import url(https://spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.a11y-hidden, legend {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin: -1px;
  -webkit-clip-path: polygon(0 0, 0 0, 0 0);
          clip-path: polygon(0 0, 0 0, 0 0);
}

.login-container {
  margin: 30px;
}

.form-container {
  background-color: #E2DAC8;
  border-radius: 25px;
  padding: 60px;
}

fieldset {
  border: 0 none;
  width: 400px;
  margin: 0 auto;
}

label > input {
  margin-left: 5%;
  width: 90%;
  height: 30px;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #2A4071;
  padding-top: 20px;
}

label > input::-webkit-input-placeholder {
  text-align: center;
  font-size: 0.9rem;
  vertical-align: bottom;
  color: rgba(42, 64, 113, 0.5);
}

label > input:-ms-input-placeholder {
  text-align: center;
  font-size: 0.9rem;
  vertical-align: bottom;
  color: rgba(42, 64, 113, 0.5);
}

label > input::-ms-input-placeholder {
  text-align: center;
  font-size: 0.9rem;
  vertical-align: bottom;
  color: rgba(42, 64, 113, 0.5);
}

label > input::placeholder {
  text-align: center;
  font-size: 0.9rem;
  vertical-align: bottom;
  color: rgba(42, 64, 113, 0.5);
}

.btn {
  background-color: #2A4071;
  color: #E2DAC8;
  border-radius: 25px;
  cursor: pointer;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 50px;
  margin-top: 40px;
  font-size: 1.2rem;
  font-weight: 900;
}
.boardContainer {
  border: 5px solid #E2DAC8;
  margin: 30px;
  border-radius: 25px;
}
.newInputBoard {
  margin-left: 5%;
  margin-top: 30px;
  width: 300px;
  height: 30px;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #E2DAC8;
  color: #E2DAC8;
  padding-left: 10px;
  font-size: 1.1rem;
}

.newInputBoard::-webkit-input-placeholder {
  vertical-align: bottom;
  text-align: left;
  /* padding-left: 10px; */
  color: rgba(226, 218, 200, 0.5);
}

.newInputBoard:-ms-input-placeholder {
  vertical-align: bottom;
  text-align: left;
  /* padding-left: 10px; */
  color: rgba(226, 218, 200, 0.5);
}

.newInputBoard::-ms-input-placeholder {
  vertical-align: bottom;
  text-align: left;
  /* padding-left: 10px; */
  color: rgba(226, 218, 200, 0.5);
}

.newInputBoard::placeholder {
  vertical-align: bottom;
  text-align: left;
  /* padding-left: 10px; */
  color: rgba(226, 218, 200, 0.5);
}
.boardSection {
  background-color: #E2DAC8;
  display: flex;
  flex-wrap: nowrap;
  margin-top: 30px;
  padding: 2% 5%;
  position: relative;
  overflow-x: auto;
  white-space: nowrap;
  border-radius: 0 0 10px 10px;
}

.boardCard {
  position: relative;
  border: 2px solid #2A4071;
  box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.25);
  list-style: none;
  padding: 0;
  width: 250px;
  min-width: 250px;
  min-height: 300px;
  margin-right: 50px;
}
.inputTodo {
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  background: #FFFDFA;
  border: none;
  border-top: 1px solid #2A4071;
  width: 100%;
  height: 30px;
  padding: 7px;
  font-size: .9rem;
}
.todos {
  display: flex;
  padding: 3px;
  border-bottom: 1px solid #2A4071;
  align-items: center;
}
.checkbox {
  display: none;
}

.checkbox + label {
  box-sizing: border-box;
  display: inline-block;
  background-color: #FFFDFA;
	width: 15px;
	height: 15px;
  cursor: pointer;
  border: 1px solid #FFFDFA;
}

.checkbox:checked + label {
  background-color: #2A4071;
}
.todo-remove {
  background-color: #E2DAC8;
  font-weight: 700;
  border: none;
  cursor: pointer;
}
.todos > span {
  flex-grow: 1;
}

.todo-content {
  margin-left: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden
}

.completed-check {
  position: relative;
}
.completed-check::after {
  content: '';
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #2A4071;
  opacity: .5;
  -webkit-animation: move .3s linear;
          animation: move .3s linear;
}

@-webkit-keyframes move {
  from {
    width: 0
  }
  to {
    width: 100%
  }
}

@keyframes move {
  from {
    width: 0
  }
  to {
    width: 100%
  }
}
.removeBtn {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #2A4071;
  color: #E2DAC8;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  cursor: pointer;
  height: 35px;
  padding: 0 10px;
}
.board-title {
  background-color: #B4B9CE;
  border-bottom: 2px solid #2A4071;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5%;
  font-size: 1.2rem;
  color: #2A4071;
  font-weight: 700;
  height: 35px;
}
body {
  background-color: #2A4071;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-sizing: border-box;
  background-color: #B4B9CE;
  padding: 20px 30px 20px 0;
  text-align: end;
  color: #2A4071;
  font-weight: 600;
  z-index: 10;
  font-family: 'Spoqa Han Sans', sans-serif;
  opacity: .9;
}

.logout {
  margin-left: 10px;
  cursor: pointer;
  text-decoration: underline;
}

.title-container {
  margin-top: 90px;
  text-align: center;
}

.title {
  font-weight: 900;
  color: #E2DAC8;
  text-shadow: 5px 5px 30px rgba(0, 0, 0, 0.5);
  font-size: 165px;
  letter-spacing: -0.05em;
}
